import moment from 'moment';
import 'moment/locale/id';

export const convertToId = time => {
	return moment(time)
		.locale('id')
		.format('dddd, Do MMMM YYYY');
};

export const convertToIdWithAdd = (time: string, addTime: string) => {
	return moment(time)
		.add(addTime, 'seconds')
		.locale('id')
		.format('dddd, Do MMMM YYYY');
};

export const getDiff = (time: string) => {
	const end = moment();
	const start = moment(time);
	var diffDuration = moment.duration(end.diff(start));

	return {
		seconds: diffDuration.seconds(),
		minutes: diffDuration.minutes(),
		hours: diffDuration.hours(),
		days: diffDuration.days(),
		months: diffDuration.months(),
		years: diffDuration.years(),
	};
};

export const toRead = (duration: number) => {
	return moment({})
		.seconds(duration)
		.format('H:mm:ss');
};
